import React from "react";
import styled from "styled-components";
import PageHeader from "../common/PageHeader";

const EducationSection = () => {
  const educationDetails = [
    {
      schoolName: "University Of Windsor",
      degree: "Master of Applied Computing",
      duration: "Jan 2024 - Apr 2024",
      location: "ON, Canada",
      logo: "uwin-logo.jpg", // Replace with the actual path or URL to the logo
      subjects: [
        "Cloud Computing",
        "Data Structures & Algorithms",
        "Database Management Systems",
        "DevOps Practices",
        "Big Data Analytics",
        "Cloud Computing",
        "Data Structures & Algorithms",
        "Database Management Systems",
        "DevOps Practices",
        "Big Data Analytics",
        
      ],
    },
    {
      schoolName: "University of Mumbai",
      degree: "Bachelor of Engineering (Information Technology)",
      duration: "Jun 2017 - May 2021",
      location: "Mumbai, India",
      logo: "uniMumbai-logo.png", // Replace with the actual path or URL to the logo
      subjects: [
        "Web Technologies",
        "Operating Systems",
        "Artificial Intelligence",
        "Computer Networks",
        "Database Management Systems",
      ],
    },
  ];

  return (
    <SectionWrapper id="education">
      <PageHeader>Education</PageHeader>
      <EducationList>
      {educationDetails.map((edu, index) => {
        const half = Math.ceil(edu.subjects.length / 2);
        const leftSubjects = edu.subjects.slice(0, half);
        const rightSubjects = edu.subjects.slice(half);

        return (
            <Card key={index}>
            <LogoWrapper>
                <img src={edu.logo} alt={`${edu.schoolName} logo`} />
            </LogoWrapper>
            <ContentWrapper>
                <CardHeader>
                <div className="left">
                    <h1>{edu.schoolName}</h1>
                    <h4>{edu.degree}</h4>
                </div>
                <div className="right">
                    <p className="duration">{edu.duration}</p>
                    <p className="location">{edu.location}</p>
                </div>
                </CardHeader>
                <SubjectList>
                <h4 class="courseWork">Relevant Coursework:</h4>
                <SubjectColumns>
                    <ul>
                    {leftSubjects.map((subject, i) => (
                        <li key={i}>{subject}</li>
                    ))}
                    </ul>
                    <ul>
                    {rightSubjects.map((subject, i) => (
                        <li key={i}>{subject}</li>
                    ))}
                    </ul>
                </SubjectColumns>
                </SubjectList>
            </ContentWrapper>
            </Card>
        );
        })}
      </EducationList>
    </SectionWrapper>
  );
};

export default EducationSection;

// Styled Components
const SectionWrapper = styled.section`
  padding: 50px 20px;
  background: ${(props) => props.theme.background};
  color: ${(props) => (props.theme.dark ? "#fff" : "#252525")};
`;

const EducationList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

const Card = styled.div`
  display: flex;
  align-items: flex-start;
  background: ${(props) => props.theme.cardBackground};
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
`;

const LogoWrapper = styled.div`
  flex: 0 0 75px;
  img {
    width: 75px;
    height: 75px;
    object-fit: contain;
    border-radius: 6px;
  }
`;

const ContentWrapper = styled.div`
  flex: 1;
  margin-left: 20px;
`;

const CardHeader = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
    h4.courseWork{
        color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#747474")};
    }
  .left {
    h1 {
      font-size: 1.2em;
      color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#555")};
      margin-top: 14px;
      }
    h4 {
      font-size: 0.9em;
      color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#555")};
      margin: 1px 0;
      }
  }

  .right {
    text-align: right;
    font-size: 0.9em;
    color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#555")};
    p.duration {
          font-size: 1em;
          margin-top: 15px;
          color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#666")};
      }
      p.location {
        font-size: 1em;
        margin-top: 13px;
        color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#666")};
      }
    }
;
`;

