const SubjectColumns = styled.div`
  display: flex;
  gap: 20px;

  ul {
    flex: 1;
    list-style-type: circle;
    padding-left: 20px;
    color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#555")};

    li {
      margin-bottom: 8px;
    }
  }
`;

const SubjectList = styled.div`
  margin-top: 20px;
    .coursework-heading {
      font-size: 1em;
      margin-top: 5px;
      color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#555")};
    }
  h4 {
    font-size: 15px;
    margin-bottom: 8px;
    color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#555")};
  }

  ul {
    list-style-type: circle;
    padding-left: 20px;
    color: ${(props) => (props.theme.dark ? "rgb(226, 226, 226)" : "#555")};
  }
`;
